import React from "react"
import { Grid } from "@mui/material"
import FeaturedProduct from "../components/Features/FeaturedProduct"
import LazyLoad from "react-lazyload"


export const ArtworkList = ({ artworks }) => {
    return <div className="container container__tight container__scroll" style={{ padding: "16px" }}>
        <Grid container spacing={4}>
            <Grid item xl={12} lg={12} md={12} sm={12} xs={12}>
                <h2>More Works.</h2>
            </Grid>
            {artworks.map((artwork, index) => {
                return <Grid item xl={4} lg={4} md={4} sm={6} xs={12} key={"artwork-" + index}>
                    <LazyLoad>
                        <FeaturedProduct feature={artwork} />
                    </LazyLoad>
                </Grid>
            })}
        </Grid>
    </div>
}